// export default Contact;

import { useState, useRef } from "react";
import { Link } from "react-router-dom";
import StatusModal from "./StatusModal";
import ReCAPTCHA from "react-google-recaptcha";

const Contact = () => {
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    phone: "",
    message: "",
  });
  const [status, setStatus] = useState("");
  const [modalOpen, setModalOpen] = useState(false);
  const [modalType, setModalType] = useState("info");
  const [recaptchaToken, setRecaptchaToken] = useState("");
  const recaptchaRef = useRef();

  const RECAPTCHA_SITE_KEY = process.env.REACT_APP_RECAPTCHA_SITE_KEY;

  // Handle reCAPTCHA change
  const handleRecaptchaChange = (token) => {
    setRecaptchaToken(token);
  };

  // Handle input change
  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.id]: e.target.value });
  };

  // Handle submit
  const handleSubmit = async (e) => {
    e.preventDefault();

    // Validate reCAPTCHA
    if (!recaptchaToken) {
      setStatus("Please complete the reCAPTCHA verification.");
      setModalType("error");
      setModalOpen(true);
      return;
    }

    // Show loading modal
    setStatus("Submitting your message...");
    setModalType("loading");
    setModalOpen(true);

    try {
      const res = await fetch(
        `${process.env.REACT_APP_BACKEND_URL}/api/contact`,
        {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            ...formData,
            recaptchaToken, // Include reCAPTCHA token
          }),
        }
      );

      const data = await res.json();

      if (res.ok) {
        setStatus(
          data.message ||
            "Your message has been sent successfully! We'll get back to you soon."
        );
        setModalType("success");
        setFormData({ name: "", email: "", message: "", phone: "" }); // reset form
        setRecaptchaToken(""); // Reset reCAPTCHA
        recaptchaRef.current.reset(); // Reset reCAPTCHA widget
      } else {
        setStatus(data.message || "Something went wrong. Please try again.");
        setModalType("error");
        setRecaptchaToken(""); // Reset reCAPTCHA on error
        recaptchaRef.current.reset(); // Reset reCAPTCHA widget
      }
    } catch (err) {
      setStatus("Server error, please check your connection and try again.");
      setModalType("error");
      setRecaptchaToken(""); // Reset reCAPTCHA on error
      recaptchaRef.current.reset(); // Reset reCAPTCHA widget
    }
  };

  // Handle modal close
  const handleModalClose = () => {
    setModalOpen(false);
    setStatus("");
  };

  return (
    <section className="contact padding-t-120 padding-b-60 section-bg position-relative z-index-1 overflow-hidden">
      <img
        src="assets/images/gradients/banner-two-gradient.png"
        alt=""
        className="bg--gradient"
      />
      <img
        src="assets/images/shapes/pattern-five.png"
        className="position-absolute end-0 top-0 z-index--1"
        alt=""
      />
      <div className="container container-two" style={{marginTop:"-90px"}}>
        <div className="row gy-4" >
          <div className="col-lg-5" style={{marginTop:"170px"}}>
            <div className="contact-info">
              <h3 className="contact-info__title">Get in Touch With Us</h3>
              <p className="contact-info__desc">
                We're here to assist you with bookings, support, or any queries.
                Reach out to us anytime — your health is our priority.
              </p>
              <div className="contact-info__item-wrapper flx-between gap-4">
                <div className="contact-info__item">
                  <span className="contact-info__text text-capitalize d-block mb-1">
                   Call Us
                  </span>
                  <Link
                    to="tel:7975854144"
                    className="contact-info__link font-24 fw-500 text-heading hover-text-main"
                  >
                    (+91) 7975854144
                  </Link>
                </div>
                <div className="contact-info__item">
                  <span className="contact-info__text text-capitalize d-block mb-1">
                    Email Us
                  </span>
                  <Link
                    to="mailto:contact@4ceps.com"
                    className="contact-info__link font-24 fw-500 text-heading hover-text-main"
                  >
                    contact@4ceps.com
                  </Link>
                </div>
              </div>
            </div>
          </div>

          <div className="col-lg-7 ps-lg-5">
            <div className="card common-card p-sm-4">
              <div className="card-body">
                <form onSubmit={handleSubmit} autoComplete="off">
                  <div className="row gy-4">
                    <div className="col-sm-12 col-xs-12">
                      <label
                        htmlFor="name"
                        className="form-label mb-2 font-18 font-heading fw-600" style={{fontFamily:"inter"}}
                      >
                        Full Name
                      </label>
                      <input
                        type="text"
                        className="common-input common-input--grayBg border"
                        id="name"
                        pattern="[A-Za-z ]+"
                        placeholder="Enter Your Name"
                        value={formData.name}
                        onChange={handleChange}
                        required
                        onKeyPress={(e) => {
                          if (!/[A-Za-z ]/.test(e.key)) {
                            e.preventDefault();
                          }
                        }}
                      />
                    </div>
                    <div className="col-sm-6 col-xs-6">
                      <label
                        htmlFor="email"
                        className="form-label mb-2 font-18 font-heading fw-600" style={{fontFamily:"inter"}}
                      >
                        Email
                      </label>
                      <input
                        type="email"
                        className="common-input common-input--grayBg border"
                        id="email"
                        placeholder="Enter Your Email"
                        value={formData.email}
                        onChange={handleChange}
                        required
                      />
                    </div>
                    <div className="col-sm-6 col-xs-6">
                      <label
                        htmlFor="phone"
                        className="form-label mb-2 font-18 font-heading fw-600" style={{fontFamily:"inter"}}
                      >
                        Phone Number
                      </label>
                      <input
                        type="tel"
                        className="common-input common-input--grayBg border"
                        id="phone"
                        placeholder="Enter Your Phone Number"
                        value={formData.phone}
                        onChange={handleChange}
                        required
                        maxLength={10}
                        onKeyPress={(e) => {
                          const char = e.key;
                          // Only digits allowed
                          if (!/[0-9]/.test(char)) {
                            e.preventDefault();
                            return;
                          }
                          // First digit must be 6,7,8,9
                          if (formData.phone.length === 0 && !/[6-9]/.test(char)) {
                            e.preventDefault();
                          }
                        }}
                      />
                    </div>
                    <div className="col-sm-12">
                      <label
                        htmlFor="message"
                        className="form-label mb-2 font-18 font-heading fw-600" style={{fontFamily:"inter"}}
                      >
                        Message
                      </label>
                      <textarea
                        className="common-input common-input--grayBg border"
                        id="message"
                        placeholder="Write Your Message Here"
                        value={formData.message}
                        onChange={handleChange}
                        required
                      />
                    </div>
                    
                    {/* reCAPTCHA Section */}
                    <div className="col-sm-12">
                      <div className="d-flex justify-content-center">
                        <ReCAPTCHA
                          ref={recaptchaRef}
                          sitekey={RECAPTCHA_SITE_KEY}
                          onChange={handleRecaptchaChange}
                          onExpired={() => setRecaptchaToken("")}
                          onErrored={() => setRecaptchaToken("")}
                        />
                      </div>
                    </div>

                    <div className="col-sm-12">
                      <button
                        type="submit"
                        className="btn btn-main btn-lg pill w-100"
                        disabled={!recaptchaToken} // Disable until reCAPTCHA completed
                      >
                        Submit Now
                      </button>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Status Modal */}
      <StatusModal
        isOpen={modalOpen}
        onClose={handleModalClose}
        status={status}
        type={modalType}
        autoClose={modalType !== "loading"}
        autoCloseDelay={modalType === "success" ? 4000 : 5000}
      />
    </section>
  );
};

export default Contact;
