import React, { useEffect } from "react";
import {
  FaCheckCircle,
  FaExclamationTriangle,
  FaTimes,
  FaSpinner,
} from "react-icons/fa";
import "./StatusModal.css";

const StatusModal = ({
  isOpen,
  onClose,
  status,
  type = "info",
  autoClose = true,
  autoCloseDelay = 3000,
}) => {
  useEffect(() => {
    if (isOpen && autoClose && type !== "loading") {
      const timer = setTimeout(() => {
        onClose();
      }, autoCloseDelay);

      return () => clearTimeout(timer);
    }
  }, [isOpen, autoClose, autoCloseDelay, onClose, type]);

  useEffect(() => {
    if (isOpen) {
      document.body.style.overflow = "hidden";
    } else {
      document.body.style.overflow = "unset";
    }

    return () => {
      document.body.style.overflow = "unset";
    };
  }, [isOpen]);

  if (!isOpen) return null;

  const getModalConfig = () => {
    switch (type) {
      case "success":
        return {
          icon: <FaCheckCircle />,
          className: "status-modal--success",
          title: "Success!",
          iconColor: "#10b981",
        };
      case "error":
        return {
          icon: <FaExclamationTriangle />,
          className: "status-modal--error",
          title: "Error!",
          iconColor: "#ef4444",
        };
      case "loading":
        return {
          icon: <FaSpinner className="spinning" />,
          className: "status-modal--loading",
          title: "Please Wait...",
          iconColor: "#3b82f6",
        };
      default:
        return {
          icon: <FaCheckCircle />,
          className: "status-modal--info",
          title: "Information",
          iconColor: "#3b82f6",
        };
    }
  };

  const config = getModalConfig();

  return (
    <div className="status-modal-overlay" onClick={onClose}>
      <div
        className={`status-modal ${config.className}`}
        onClick={(e) => e.stopPropagation()}
      >
        <div className="status-modal__header">
          <div
            className="status-modal__icon"
            style={{ color: config.iconColor }}
          >
            {config.icon}
          </div>
          {type !== "loading" && (
            <button
              className="status-modal__close"
              onClick={onClose}
              aria-label="Close modal"
            >
              <FaTimes />
            </button>
          )}
        </div>

        <div className="status-modal__body">
          <h3 className="status-modal__title">{config.title}</h3>
          <p className="status-modal__message">{status}</p>
        </div>

        {type !== "loading" && (
          <div className="status-modal__footer">
            <button className="status-modal__button" onClick={onClose}>
              {type === "success" ? "Continue" : "Try Again"}
            </button>
          </div>
        )}

        {autoClose && type !== "loading" && (
          <div className="status-modal__progress">
            <div
              className="status-modal__progress-bar"
              style={{ animationDuration: `${autoCloseDelay}ms` }}
            ></div>
          </div>
        )}
      </div>
    </div>
  );
};

export default StatusModal;
